import React from "react"
import { StaticImage } from "gatsby-plugin-image"

export default function Proyectos() {
  return (
    <section id="proyectos" className="w-full">
      <h2 className="text-white text-center subtitulo teko uppercase mb-10">Proyectos <span
        className="texto-rojo">recientes</span></h2>

      {/* DataTics */}
      <article className="rounded-xl lighter-black text-white  mb-28 article">
        <h3 className="subsubtitulo poppins">DataTics</h3>
        <div className="py-4 sm:py-8 md:py-16">
          <StaticImage
            src="../images/Proyectos/DataTics.png"
            alt="DataTics HomePage" />
        </div>
      </article>

      {/* Covid Por Partido */}
      <article className="rounded-xl lighter-black text-white  mb-28 article">
        <h3 className="subsubtitulo poppins">COVID por partido</h3>
        <div className="py-4 sm:py-8 md:py-16">
          <StaticImage
            src="../images/Proyectos/COVIDPorPartido.png"
            alt="COVID por partido HomePage" />
        </div>
      </article>

      {/* SCCEG */}
      <article className="rounded-xl lighter-black text-white  mb-28 article">
        <h3 className="subsubtitulo poppins">SCCEG</h3>
        <div className="py-4 sm:py-8 md:py-16">
          <StaticImage
            src="../images/Proyectos/SCCEG.png"
            alt="SCCEG HomePage" />
        </div>
      </article>

      {/* Cortes de caja */}
      <article className="rounded-xl lighter-black text-white  mb-28 article">
        <h3 className="subsubtitulo poppins">Cortes de caja (Mejor nombre pendiente)</h3>
        <div className="py-4 sm:py-8 md:py-16">
          <StaticImage
            src="../images/Proyectos/CortesDeCaja.png"
            alt="Cortes de caja HomePage" />
        </div>
      </article>

      {/* ESCPOS */}
      <article className="rounded-xl lighter-black text-white  mb-28 article">
        <h3 className="subsubtitulo poppins break-words">github.com/EduardoConX/escpos</h3>
        <div className="py-4 sm:py-8 md:py-16">
          <StaticImage
            src="../images/Proyectos/ESCPOS.png"
            alt="ESCPOS HomePage" />
        </div>
      </article>
    </section>

  )
}
