import React from "react";

export default function Enlace({ children, enlace, desc }) {    
    return(
        <a href={enlace} target="_blank" rel="noopener noreferrer"
          className="w-1/3 color-shadow p-10 flex flex-col justify-between items-center rounded-3xl">
          <div className="circle bg-colors rounded-full flex justify-center items-center">
            {children}
          </div>
          <p className="text-white subsubtitulo my-8 teko text-center">{desc}</p>
        </a>
    );
}